import "./style.css";

// import renderBoard from "./domModule";

const select = (selector) => document.querySelector(selector);
const updateContainerMargin = () => {
  const header = select("header");
  const footer = select("footer");
  const headerHeight = header.offsetHeight;
  const footerHeight = footer.offsetHeight;

  document.documentElement.style.setProperty(
    "--header-height",
    `${headerHeight}px`
  );
  document.documentElement.style.setProperty(
    "--footer-height",
    `${footerHeight}px`
  );
};

window.addEventListener("load", updateContainerMargin);
window.addEventListener("resize", updateContainerMargin);

const humanContainer = select(".human .game-container");
const aiContainer = select(".ai .game-container");
