*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* general  */

:root {
  --header-footer: #ffdc00;
  --sea: #7fdbff;
  --land: #001f3f;
  --ships: #333333;
  --logo: #0c0606;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
}

main {
  height: 100vh;
  width: 100vw;

  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-color: var(--sea);
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

header .logo {
  width: clamp(150px, 20vw, 250px);
}

.headerWrapper,
.footerWrapper {
  width: 100%;
  background-color: var(--land);
  position: fixed;
  z-index: 1;
  display: flex;
  justify-content: center;
}

header,
footer {
  position: fixed;
  max-width: 1200px;
  min-width: 400px;
  width: 100%;
}

header {
  height: max-content;
  top: 0;
  padding: 10px;
}

.headerWrapper {
  top: 0;
  height: var(--header-height);
}

footer,
.footerWrapper {
  bottom: 0;
  height: var(--header-height);
}

footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

footer > a {
  text-decoration: none;

  font-size: clamp(1rem, 2vw, 1.5rem);
  color: var(--sea);
  border-radius: 5px;
  padding: 0 10px;
}

footer > a:hover {
  background-color: var(--sea);
  color: var(--land);
}
.container {
  width: 100%;
  max-width: 1200px;
  min-width: 400px;
  flex: 1;
  display: flex;

  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: var(--header-height);
  margin-bottom: var(--footer-height);
}

h2 {
  margin-top: 20px;
  background-color: white;
  padding: 0 10px;
  border-radius: 5px;
  color: var(--sea);
}

.game {
  display: flex;
  gap: 50px;
  border: 2px solid;
}

/* gameboard  */

.game-container {
  width: 100%;
  display: grid;
  grid-template:
    ".... col-indices " 20px
    "row-indices board " 1fr/ 20px 1fr;
  gap: 10px;
  background-color: white;
  padding: 20px;
  border-radius: 15px;
  position: relative;
}

.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 3;
}

.row-indices {
  grid-area: row-indices;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.index {
  width: clamp(20px, 3vw, 40px);
  height: clamp(20px, 3vw, 40px);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: clamp(1rem, 1.5vw, 1.5rem);
}

.col-indices {
  grid-area: col-indices;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.board {
  grid-area: board;
  display: flex;
  flex-direction: column;
}

.board-row {
  display: flex;
}

.cell {
  width: clamp(25px, 3vw, 45px);
  height: clamp(25px, 3vw, 45px);
  border: 1px solid #ccc;
  margin-right: 5px;
  margin-bottom: 5px;
}

.human,
.ai {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
